.square{
    padding: 10px;
    width: 40px;
    height: 40px;
    text-align: center;
    font-size: 30px;
}
.square:hover{
    background-color: rgba(235, 84, 84, 0.31);
    border-radius: 40%;
    transition: all .3s;
    cursor: pointer;
   
    transform: scale(1.1);
}
 
.sq-container{
    border: 1px solid rgb(0, 0, 0);
}